<template>
  <v-container
    id="listarCampaas"
    fluid
    tag="section"
  >
    <v-btn
      to="/ventas/campaas/nuevo"
      fab
      dark
      color="purple"
      class="mx-5"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>

    <v-btn
      to="/ventas"
      fab
      dark
      color="black"
      class="mx-5"
    >
      <v-icon>mdi-arrow-left</v-icon>
    </v-btn>

    <base-material-card
      icon="mdi-clipboard-text"
      title="Lista de Campaas"
      class="px-5 py-3"
    >
      <v-data-table
        :headers="cabecera"
        :items="campanias"
        sort-by="Campaa"
        class="elevation-1"
      >
        <template v-slot:item.actions="{ item }">
          <v-btn
            :to="{ name: 'Editar Campaa', params: { id: item._id } }"
            fab
            small
            color="light-blue"
          >
            <v-icon> mdi-pencil </v-icon>
          </v-btn>
          <v-btn
            fab
            small
            color="red"
            class="mx-5"
            @click.stop="dialog = true"
            @click="id = item._id"
          >
            <v-icon> mdi-delete </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </base-material-card>

    <v-dialog
      v-model="dialog"
      width="350"
    >
      <v-card>
        <v-card-title class="headline grey lighten-2">
          Desea eliminar el registro?
        </v-card-title>

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn @click="dialog = false">
            Cancelar
          </v-btn>
          <v-btn
            color="error"
            @click="confirmarBorrado(id)"
          >
            Aceptar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
  import axios from 'axios'
  const urlCampaa = 'https://sistema-adv.herokuapp.com/api/campaign/'

  export default {
    name: 'ListarCampaAs',
    data () {
      return {
        dialog: false,
        cabecera: [
          { text: 'Campaa', value: 'name', align: 'center' },
          { text: 'Oferta', value: 'descripcion', align: 'center' },
          { text: 'Bono', value: 'bono', align: 'center' },
          { text: 'Fecha Inicio', value: 'startDate', align: 'center' },
          { text: 'Fecha Fin', value: 'endDate', align: 'center' },
          {
            text: 'Acciones',
            value: 'actions',
            align: 'center',
            sortable: false,
          },
        ],
        campanias: [
          {
            name: '',
            descripcion: '',
            bono: '',
            startDate: '',
            endDate: '',
          },
        ],
      }
    },
    mounted () {
      this.obtenerCampanias()
    },
    methods: {
      obtenerCampanias () {
        axios
          .get(urlCampaa)
          .then((response) => {
            // console.log(response.data)
            this.campanias = response.data
          })
          .catch((err) => {
            console.log(err)
          })
      },
      confirmarBorrado (id) {
        axios
          .delete(urlCampaa + id)
          .then((res) => {
            // console.log(id);
            this.obtenerCampanias()
            this.dialog = false
          })
          .catch((e) => {
            console.log(e)
          })
      },
    },
  }
</script>
